
.bills > ul {
  list-style-type: none;
  padding: 0;
  width: 100%;
  display: block;
}
.bills > ul > li .date {
  width: 20%;
  float: left;
  display: block;
}
.bills > ul > li .title {
  width: 50%;
  float: left;
  display: block;
}
.bills > ul > li .value {
  width: 30%;
  float: left;
  display: block;
}



